<template>
  <div>
    <top-bar :secondPage="topBar"></top-bar>
    <room-list :list="roomList"></room-list>
  </div>
</template>

<script>
import { roomAPI } from '@/api/api'
import roomList from '@/components/roomList.vue'
import TopBar from '@/components/TopBar.vue'
export default {
  data () {
    return {
      topBar: '寝室学生联系方式查询',
      roomDataList: [],
      roomList: []
    }
  },
  components: { roomList, TopBar },
  created () {
    const id = this.$route.query.id
    console.log(id)
    this.getDormitoryDeatil(id)
  },
  methods: {
    async getDormitoryDeatil (id) {
      const res = await roomAPI(id)
      this.roomDataList = res.data
      // console.log(res.data)
      const array1 = res.data
      const groupByCategory = array1.reduce((group, product) => {
        const { stuBedroom } = product
        group[stuBedroom] = group[stuBedroom] ?? []
        group[stuBedroom].push(product)
        return group
      }, {})
      // 1.使用map()函数，对键值对遍历，并使用解构赋值将键（key）和值（value）分别赋给变量
      // 2.创建一个新的对象，包含groupName和data属性，分别对应命名组和对应的数组
      // 3.将对象存储在namedArrays数组中
      const newArr = Object.entries(groupByCategory).map(([key, value]) => ({
        groupName: key,
        data: value
      }))
      this.roomList = newArr
    }
  },
  watch: {
    '$route.query.id': {
      handler () {
        this.$router.go(0)
      }
    }
  }
}
</script>

